@media only screen and (max-width: 1000px) {

  .statement-container {
    margin-top: 2rem;
    margin-right: 1rem;
    margin-bottom: 1rem;
  }

  .form,
  .form input,
  .message {
    width: 100%;
    margin: 0;
  }

  div.contact-page-container {
    flex-direction: column;
    margin-bottom: 0;
  }
  .statement-container p{
    margin-top: 6vh;
    margin-right: 1rem;
    margin-left: 1rem;
    text-align: center;
  }


}