.tabtn:hover {
  background: rgb(35, 34, 35) !important;
  color: white !important;
  box-shadow: 0 6px 40px #041106, 0 6px 15px #0c0707;
  transition: transform ease-in-out 0.2s;
  transform: scale(1.15);
}

.tabtn {
  background: #fffd0e !important;
  color: #130b0b !important;
  width: 10rem;
  text-align: center;
}

.tabrow .tabtn .active {
  background: #130b0b;
}

hr {
  padding: 1rem;
}

.privacy-links {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.links-box {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-around;
  padding-bottom: 3rem;
}


#privacy-page-link {
  /* position: relative;
  left: 74vw;
  bottom: 3vh;*/
  margin: 1rem;
  padding-top: 3px;
  padding-bottom: 3px;
  padding-left: 5px;
  padding-right: 5px;
  border: 2px solid #fffd0e;
  border-radius: 5px;
  max-height: 2rem;
  color: #4ca2ff;
}

.privacy-page-link:hover {
  text-decoration: underline;
  color: #236197 !important;

}

.contact {
  display: flex;
  flex-direction: column;
}

p {
  display: inline;
}

.phone {
  color: #4ca2ff;
  cursor: pointer;
  padding-left: 1rem;
}

.phone:hover {
  text-decoration: underline;
  color: #236197;
}

.email{
  padding-left: 22px;
  color: #4ca2ff;
}

.footer-contact {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  /*padding:4rem;
  margin: 2rem;*/
}

.email:hover {
  text-decoration: underline;
  color: #236197;
}


@media only screen and (max-width: 436px) {

  .email,
  .privacy-links {
    padding: 0;
    margin-left: 1rem;
  }

  .phone {
    margin-left: 0;
  }

  a.email,
  p {
    display:grid;
    margin-left: 3px;
    padding-left: 0;
  }

}